
body{
  font-family: 'Rubik', sans-serif;
  margin: 0;
  padding: 0;
  color: #4F5665;
}
a {
text-decoration: none;
color: #4F5665;
}

.container1{
  margin: 0 250px;
  margin-top: 100px;
}

.container{
  margin: 0 350px;
  margin-top: 50px;
}

.center{
  text-align: center;
}



.flex{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex-start{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.big{
  color: #0B132A;
  font-weight: 700;
  font-size: 50px;
}

.main-button{
  font-weight: 700;
  font-size: 16px;
  color: white;
  text-decoration: none;
  padding: 20px 40px;
  outline: none;
  border-width: 2px 0;
  border-style: solid none;
  border-radius: 30px;
  background: #F53838;
  transition: 0.2s;
  width: 100px;
}
.main-button:hover {
  background: #f00707;
}

.head-button {
  font-weight: 700;
  font-size: 16px;
  color: #F53855;
  text-decoration: none;
  padding: 20px 40px;
  border-color: #F53855;
  border-radius: 30px;
  transition: 0.2s;
}

.header img{
  margin-right: 20px;
}
.menu a{
  margin-right: 40px;
  color: #0B132A;
  font-weight: 400;
  font-size: 16px;
}
.registartion a{
  margin-right: 20px;
  color: #0B132A;
  font-weight: 500;
  font-size: 16px;
}
.banner-left h1{
  font-weight: 500;
  font-size: 50px;
  color: #0B132A;
}
.banner-left .main-button {
  margin-top: 50px;
}

.banner-down{
  margin-top: 170px;
}
.banner-down img{
  margin-right: 20px;
  width: 55px;
}



.banner-down-text h2{
  font-weight: 500;
  font-size: 25px;
  color: #0B132A;
}

.banner-down-text h3{
  font-weight: 400;
  font-size: 20px;
  color: #4F5665;
}
.about-flex{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.about-right{
  margin-left: 169px;
}

.about-right h2 {
  font-weight: 700;
  font-size: 35px;
  color: #0B132A;
}

.about-right h3 {
  font-weight: 400;
  font-size: 16px;
  color: #4F5665;
}

.about-right p{
  font-weight: 400;
  font-size: 14px;
  color: #4F5665;
  margin-left: 10px;
}
.plan h1{
  font-weight: 700;
  font-size: 35px;
  color: #0B132A;
}
.plan h2{
  font-weight: 400;
  font-size: 16px;
  color: #4F5665;
}
.plan-card{
  margin-top: 40px;
  justify-content: space-evenly;
  padding: 40px;
  border: 2px solid #DDDDDD;
}
.plan-card:hover{
  border: 2px solid #F53838;
}
.plan-card h3{
  font-weight: 700;
  font-size: 18px;
  color: #0B132A;
}

.plan-card p{
  margin-left: 10px;
  font-weight: 400;
  font-size: 14px;
  color: #4F5665;
}
.plan-down{
  font-weight: 700;
  font-size: 25px;
  color: #0B132A;
}
.earth h1{

  font-weight: 700;
  font-size: 35px;
  color: #0B132A;
}

.earth h2{
  font-weight: 400;
  font-size: 16px;
  color: #4F5665;
}

.earth div{
  margin-top:40px;
  font-weight: 400;
  font-size: 16px;
  color: #4F5665;
}

.reviews h1{
  font-weight: 700;
  font-size: 35px;
  color: #0B132A;
}

.reviews h2{
  font-weight: 400;
  font-size: 16px;
  color: #4F5665;
}

.review-card{
  padding: 0px 20px;
  margin: 20px 20px;
}

.review-card h3{
  font-weight: 700;
  font-size: 18px;
  color: #0B132A;
}
.review-card p{
  font-weight: 400;
  font-size: 14px;
  color: #4F5665;
}
.review-card-down p{
  font-weight: 400;
  font-size: 16px;
  color: #0B132A;
}

.review-card:hover{
 
  border: 2px solid #F53838;
  border-radius: 20px;
}
.review-card img{
  margin: 0 10px;
}

.subscribe-text h2{
  font-weight: 700;
  font-size: 35px;
  color: #0B132A;
}

.subscribe-text p{
  font-weight: 400;
  font-size: 16px;
  color: #4F5665;
}

.footer-left img{
  margin-right: 10px;
}

.footer-right.flex{
  align-items: unset;
}

.footer-card{
  
  margin-right: 120px;
}

@media screen and (max-width: 900px) {
  .container{
    margin: 50px 70px;
  }
  .container1{
    margin: 50px 70px;
  }
  .menu{
    display: none;
  }

  .flex{
    flex-wrap: wrap;
  }
  .registartion{
   
    margin-top: 20px;
    flex-wrap: nowrap;
    padding: 10px 20px;
  }
  .head-button{
    margin-left: 40px;
  }
  img{
    max-width: 250px;
  }
  .banner-down{
    flex-wrap: nowrap;
  }
  .about-right{
    text-align: center;
    margin: 0 50px;
    width: auto;
  }
  .plan h1,.plan h2{
    text-align: center;
    width: 250px;
  }
  .earth{
    text-align: center;
    width: 250px;
  }
  iframe{
    width: 250px;
    height: 150px;
  }
  .partners img{
    width: 250px;
    text-align: center;
  }
  .reviews {
    text-align: center;
    width: 250px;
  }
  .review-card{
    padding: 5px;
  }
  .review-card-flex{
    flex-wrap: nowrap;
  }
  .review-card-up{
    text-align: left;
    margin-right: 10px;
  }
  .subscribe-text {
    text-align: center;
  }
  .footer{
    text-align: center;
    
  }
  .flex-start{
    justify-content: center;
  }
  .footer-card{
    text-align: center;
    width: 250px;
  }
}

@media screen and (min-width: 900px  ) and (max-width: 1500px){

  .menu{
    display: none;
  }

  .flex{
    flex-wrap: wrap;
    justify-content: space-around;
  }
  img{
    width: 500px;
  }
  .banner-down{
    flex-wrap: nowrap;
  }
  .about-right{
    margin-left: 0px;
  }
  .about-flex img{
    width: 20px;
  }
  .plan-card img{
    width: 100px;
  }
  .about-flex img{
    width: 20px;
  }
  iframe{
    width: 550px;
    height: 350px;
  }
  .partners img{
    width: 100px;
  }
  .reviews{
    justify-content: space-around;
  }
 
  .review-card-flex img{
    width: 40px;
  }
  .footer-left img{
     width: 40px;
  }
  .footer-card{
    margin-top: 90px;
    margin-right: 80px;
  }
}







